.weatherMain-box {
  text-align: center;
  font-family: "Montserrat", sans-serif;
}
.weatherMain-box .main {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 16px;
  padding: 15px 25px;
  color: white;
  font-size: 18px;
  font-weight: 200;
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
  justify-content: space-evenly;
  align-items: center;
}

